@import url(https://fonts.googleapis.com/css?family=Cookie);
* {
	margin: 0;
}
body {
	margin: 0;
	font-size: 40px;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
	--deenify-color: #2196f3;
	--deenify-background: #e6ecf0;
	--deenify-primaryText: black;
	--deenify-secondaryText: grey;
}

.home {
	display: flex;
	height: 100vh;
	max-width: 1300px;
	margin-left: auto;
	margin-right: auto;
	padding: 0 10px;
}

.sidebar {
	border: 1px solid var(--deenify-background);
	flex: 0.3 1;
	padding-left: 20px;
	padding-right: 20px;
}

.sidebar-column {
	padding: 10px;
}

.sidebar-deenify-icon {
	color: var(--deenify-color);
	font-size: 30px;
	margin-left: 20px;
	margin-top: 20px;
}
.sidebar-post-button {
	background-color: var(--deenify-color) !important;
	border: none !important;
	color: white !important;
	font-weight: 900 !important;
	text-transform: inherit !important;
	border-radius: 30px !important;
	height: 50px !important;
	margin-top: 20px !important;
}

.sidebarOptions {
	display: flex;
	align-items: center;
	cursor: pointer;
	text-decoration: none;
	color: black;
}

.icons {
	padding: 20px;
	align-items: center;
}

.sidebarOptions > h2 {
	font-size: 20px;
	font-weight: 800;
	margin-right: 20px;
	text-decoration: none;
}

.sidebarOptions:hover {
	background-color: #e8f5fe;
	border-radius: 30px;
	color: var(--deenify-color);
	transition: color 100ms ease-out;
}

.sidebarOptions--active {
	color: var(--deenify-color);
}

.apptitle > h1 {
	color: var(--deenify-color);
	text-align: center;
	margin-top: 20px;
	font-family: 'Cookie', cursive;
}

.divider {
	border: 1px solid var(--deenify-background);
}

.feed {
	flex: 0.4 1;
	padding: 10px;
	border: 1px solid var(--deenify-background);
	min-width: 660px;
	overflow-y: scroll;
	-ms-overflow-style: none;
	scrollbar-width: none;
	/* width: 600px; */
}

.feed::-webkit-scrollbar {
	display: none;
}

.feed-header {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background-color: white;
	z-index: 100px;
	/* border: 1px solid var(--deenify-background); */
	/* padding: 15px 20px; */
}

.feed-header > h2 {
	font-size: 20px;
}

.searchIcon {
	color: var(--deenify-color);
}

.postBox {
	padding-bottom: 10px;
	border: 8px solid var(--deenify-background);
	padding-right: 10px;
	border-radius: 20px;
}

.postBox > form {
	display: flex;
	flex-direction: column;
}

.postBox_input {
	padding: 20px;
	display: flex;
}

.postBox_input > input {
	flex: 1 1;
	margin-left: 20px;
	font-size: 20px;
	border: none;
}

.postBox_imageInput {
	border: none;
	padding: 10px;
}
.postBox_button {
	background-color: var(--deenify-color) !important;
	border: none !important;
	color: white !important;
	font-weight: 900 !important;
	text-transform: inherit !important;
	border-radius: 30px !important;
	height: 50px !important;
	width: 80px;
	margin-left: auto !important;
	margin-top: 20px !important;
}

.post {
	display: flex;
	align-items: flex-start;
	border-bottom: 2px solid var(--deenify-background);
	padding: 10px;
}
.post_body > img {
	border-radius: 20px;
	width: 600px;
}
.post-header {
	display: flex;
	justify-content: space-between;
}
.post-header-items {
	display: flex;
}

.post_headerText {
	font-size: 15px;
	margin: 0px;
}

.post-header-name-and-time {
	padding-left: 10px;
}

.post-header-time {
	margin: 0px;
	padding-left: 5px;
	font-size: 10px;
	color: var(--deenify-secondaryText);
}

.more-icon {
}

.post_body {
	flex: 1 1;
	padding: 10px;
}

.post_description {
	width: 600px;

	font-size: 15px;
}

.post-image {
	margin-top: 10px;
}

.reactions {
	display: flex;
	justify-content: space-between;
	font-size: 13px;
	padding: 5px;
	color: var(--deenify-secondaryText);
}
.reactions-count {
	margin: 0px;
	display: flex;
	justify-content: space-between;
	/* color: var(--deenify-secondaryText); */
}

.post_footer {
	display: flex;
	justify-content: space-between;
	padding: 5px;
	color: var(--deenify-secondaryText);
	font-weight: 500;
	font-size: 13px;
}

.trending {
	padding: 10px;
	flex: 0.3 1;
	border: 1px solid var(--deenify-background);
}
.trending-column {
	padding: 10px;
}

.search_input {
	display: flex;
	align-items: center;
	background-color: var(--deenify-background);
	padding: 10px;
	border-radius: 20px;
	margin-top: 20px;
	/* margin-left: 20px; */
}

.search_input > input {
	border: none;
	background-color: var(--deenify-background);
	inline-size: 450px;
}

.trending_header {
	padding: 10px;
}

.trending_post {
	margin-top: 10px;
	margin-bottom: 10px;
	border-bottom: 2px solid var(--deenify-background);
}
.trendingPost_avatar {
}
.trendingPost_headerText {
	font-size: 15px;
	padding: 10px;
}
.trendingPost_header {
	display: flex;
	align-items: center;
}

.trendingPost_Description {
	font-size: 20px;
}

.trendingPost_footer {
	padding: 10px;
	display: flex;
	justify-content: space-between;
}

.signup {
	margin-top: 100px;
}

.signup_btn {
	background-color: var(--deenify-color) !important;
}

.login-link {
	color: var(--deenify-color) !important;
}

.login {
	margin-top: 100px;
}
.signin_btn {
	background-color: var(--deenify-color) !important;
}
.signup-link {
	color: var(--deenify-color) !important;
}

.forgot-password-link {
	color: var(--deenify-color) !important;
}

.forgot-password {
	margin-top: 100px;
}

.privacy_policy {
    font-size: 15px;
    padding: 20px;
}

.horizontal-rule {
    border: 1px dotted gray;
    display: block;
    width: 40%;
    margin: auto;
}
.privacy_section_notes {
    padding-bottom: 30px;
    line-height: 2em;
}

.privacy_section_header{
    padding-top: 30px;
}
.privacy_section_subheader{
    padding: 15px 0;
}
.privacy_header {
    text-align: center;
}

.basmalah {
    text-align: center;
}
